@font-face {
  font-family: "HelveticaLight";
  src: url("../fonts/HelveticaLight/HelveticaLight.eot");
  src: url("../fonts/HelveticaLight/HelveticaLight.eot?#iefix") format("embedded-opentype"), url("../fonts/HelveticaLight/HelveticaLight.woff") format("woff"), url("../fonts/HelveticaLight/HelveticaLight.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "HelveticaMedium";
  src: url("../fonts/HelveticaMedium/HelveticaMedium.eot");
  src: url("../fonts/HelveticaMedium/HelveticaMedium.eot?#iefix") format("embedded-opentype"), url("../fonts/HelveticaMedium/HelveticaMedium.woff") format("woff"), url("../fonts/HelveticaMedium/HelveticaMedium.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}
* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
html {
  width: 100%;
  height: 100%;
  font-size: 100%;
}
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-size: 1em /* 16px */;
  line-height: 1.25 /* 20px */;
  font-family: "HelveticaLight", sans-serif;
  color: #333;
}
.page-wrapper {
  position: relative;
  min-height: 100%;
  overflow: hidden;
}
.empty {
  height: 100px;
}
.cf:before,
.row:before,
.cf:after,
.row:after {
  display: table;
  content: " ";
  clear: both;
}
.container {
  max-width: 1920px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}
.row {
  margin-left: -15px;
  margin-right: -15px;
}
h1,
h2 {
  font-family: "HelveticaMedium", sans-serif;
}
.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.logo {
  margin: 0;
  padding: 0;
  display: block;
  max-width: 320px;
  margin-top: 15px;
  margin-bottom: 15px;
}
.logo__img {
  margin: 0;
  padding: 0;
  display: block;
  width: 100%;
  height: auto;
  max-height: 70px;
}
.intro {
  background-image: linear-gradient(45deg, #eecfba, #c5dde8);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.intro__left {
  float: left;
  width: 66.6666666667%;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 180px;
}
.intro__right {
  float: left;
  width: 33.3333333333%;
}
.info__title {
  margin: 0;
  padding: 0;
  font-size: 40px;
  margin-bottom: 20px;
}
.info__text {
  margin: 0;
  padding: 0;
  text-align: justify;
}
.service {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-left: -15px;
  margin-right: -15px;
  margin-top: 40px;
}
.service__item {
  margin: 0;
  padding: 0;
  float: left;
  width: 25%;
  padding-left: 15px;
  padding-right: 15px;
  position: relative;
  text-align: center;
}
.service__link {
  margin: 0;
  padding: 0;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.service__img {
  margin: 0;
  padding: 0;
  display: block;
  margin: 0 auto;
  width: 100%;
  max-width: 64px;
  height: auto;
  transition: 0.25s ease max-width;
}
.service__title {
  margin: 0;
  padding: 0;
  font-size: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  transition: 0.25s ease color;
}
.service__text {
  margin: 0;
  padding: 0;
  max-height: 100px;
  overflow: hidden;
  transition: 0.25s ease color;
}
.service__link:hover ~ .service__title,
.service__link:hover ~ .service__text {
  color: #8b0000;
}
.service__link:hover ~ .service__img {
  max-width: 74px;
}
.image {
  position: relative;
  overflow: hidden;
}
.image_square {
  padding-top: 168.75%;
}
.image__img {
  margin: 0;
  padding: 0;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  max-width: 100%;
  transform: translate(-50%, -50%);
}
.footer {
  position: relative;
  min-height: 100px;
  margin-top: -100px;
  background-color: #222;
}
.footer__copyright {
  text-align: center;
  line-height: 100px;
  color: #fff;
}
.footer__copyright span {
  margin: 0;
  padding: 0;
  margin-left: 5px;
}
